import { Component} from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { RestProvider } from '../../providers/rest/rest'
import { BrowserPage } from '../browser/browser'
import { LoadingService } from "../../providers/util/loading.service"


@IonicPage()
@Component({
  selector: 'page-faq',
  templateUrl: 'faq.html',
})
export class FaqPage {



faqs:any;

  constructor(public loader: LoadingService, public restProvider: RestProvider, public navCtrl: NavController, public navParams: NavParams) {
  
    this.loader.presentWithMessage("Por favor, aguarde");
    
    this.loader.dismiss().then(() =>{
      this.getFAQs();
    });



   
  
  }

  getFAQs() {
    this.restProvider.getRest('{"operacao":"getfaqs"}')
    .then(data => {
    this.faqs = data;
        });
    }
    goToPesquisa(){      
      this.navCtrl.push('PesquisacursoPage')

    }

  NavigateTo(url:string){
    console.log('url: '+url)
    this.navCtrl.push(BrowserPage, url);

  }

}



